import React, { useState, useContext, useEffect } from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";

import ListItem from "@material-ui/core/ListItem";
import InputBase from "@material-ui/core/InputBase";
import List from "@material-ui/core/List";

import MultipleDestinations from "@cvccorp-components/icons/dist/MultipleDestinations";
import Clock from "@cvccorp-components/icons/dist/Clock";
import ArrowLeft from "@cvccorp-components/icons/dist/ArrowLeft";
import ErrorIcon from "@cvccorp-components/icons/dist/ErrorIcon";

import Typography from "../../Typography";
import Box from "../../Box";
import TextField from "../../TextField";
import Overlay from "../../Overlay";
import Animate from "../../Animate";
import { styles } from "../styles";
import { BrandContext } from "../../BrandContext/BrandContext";

const mergeClasses = (classes) => classes.join(" ");

const tourClasses = theme => ({
    fieldContainer: {
        position: "relative",
    },
    destinationscontainer: {
        position: "absolute",
        top: "50%",
        left: "50px",
        maxWidth: "calc(100% - 50px)",
        overflowY: "auto",
        width: "auto",
        display: "inline-flex",
        transform: "translateY(-50%)",
        whiteSpace: "nowrap",
    },
    destinationField: {
        margin: "0 4px",
        padding: "4px 10px",
        border: "1px solid",
        borderRadius: "16px",
        fontSize: "12px",
        fontWeight: 700,
    },
    destinationFieldTransparent: {
        color: theme.palette.white.main,
        borderColor: theme.palette.white.main,
    },
    destinationInput: {
        color: "transparent",
    },
    closeButton: {
        width: "24px",
        marginLeft: "8px",
        marginRight: "-4px",
        color: theme.palette.secondary.main,
    },
    destinationInputLabel: {
        paddingLeft: "8px",
    }
});

const PesqincTour = ({
    classes,
    onChange,
    onFocus,
    onClose,
    onSelect,
    value,
    selectedIndex,
    transparent,
    search,
    isMobile,
    error,
    errorMsg,
    history,
    mostSearched,
    destinations: initialDestinations,
}) => {
    const [index, setIndex] = useState(selectedIndex || 0);
    const [searching, setSearching] = useState(false);
    const [selectedItem, setSelectedItem] = useState(null);
    const [loaded, setLoaded] = useState(false);
    const [destinations, setDestinations] = useState(initialDestinations ? initialDestinations : []);
    const { theme: brandTheme } = useContext(BrandContext);

    useEffect(() => {
        setLoaded(true);
    }, []);

    useEffect(() => {
        if (initialDestinations) {
            setDestinations(initialDestinations);
            onSelect(initialDestinations);
        }
    }, [initialDestinations]);

    const addDestination = (destination) => {
        const destinationName = destination.name;
        if (destinations.find(d => d.name?.indexOf(destinationName) !== -1 || false)) {
            return;
        }
        const newDestinations = destinations;
        newDestinations.push(destination);
        setDestinations(newDestinations);
    };

    const removeDestination = (destination) => {
        const destinationName = destination.name;
        const newDestinations = destinations.filter(d => d.name !== destinationName);
        setDestinations(newDestinations);
        onSelect(newDestinations);
    };

    const handleSelect = (item, index) => {
        setSearching(false);
        setSelectedItem(item);
        addDestination(item);
        onSelect(destinations);
    };

    const handleKeyUp = e => {
        if (search && search.length > 0) {
            if (e.keyCode === 40) setIndex((index + 1) % search.length);
            else if (e.keyCode === 38)
                setIndex((index - 1 + search.length) % search.length);
            else if (e.keyCode === 13 && searching) {
                addDestination(search[index]);
                onSelect(destinations);
                setSearching(false);
            }
        }
    };

    const handleChange = e => {
        if (onChange) onChange(e);
    };

    const handleFocus = e => {
        setSearching(true);
        if (onFocus) onFocus(e);
    };

    const handleClose = () => {
        if (window !== undefined && isMobile) {
            window.scrollTo(0, 0);
        }
        if (!selectedItem || value !== selectedItem.name) {
            setSelectedItem(null);
        }

        setSearching(false);
        if (onClose) onClose(selectedItem);
    };

    const [isOpenAnimation, setOpenAnimation] = useState(false);

    const onOpenAnimate = () => {
        setOpenAnimation(true);
        document.getElementById("pesqinc-input").focus();
    };
    const onCloseAnimate = () => {
        setOpenAnimation(false);
    };

    const renderDestinationsContainer = () => {
        if (!destinations || !destinations[0]) {
            return;
        }
        const renderDestination = (destination, index) => {
            return (
                <div key={`rDest-${index}`} className={mergeClasses([classes.destinationField, classes.destinationFieldTransparent])}>
                    <Typography>{destination.name}</Typography>
                    <span onClick={() => removeDestination(destination)}>
                        <ErrorIcon
                            className={classes.closeButton}
                        />
                    </span>
                </div>
            );
        };
        return (
            <div className={classes.destinationscontainer}>
                {destinations.map((d, i) => renderDestination(d, i))}
            </div>
        );
    };

    const textLabel = () => destinations[0] ? "" : "Para onde vai?";

    return (
        <div className={classes.container}>
            {loaded && (
                <div className={mergeClasses([classes.fieldContainer, "fieldContainer"])}>
                    <TextField
                        label={textLabel()}
                        size="large"
                        transparent={transparent}
                        onClick={handleFocus}
                        onChange={handleChange}
                        onKeyUp={handleKeyUp}
                        inputClassName={mergeClasses([classes.input, classes.destinationInput])}
                        labelClassName={mergeClasses([classes.input, classes.destinationInputLabel])}
                        error={error}
                        errorMsg={errorMsg}
                        icon={<MultipleDestinations noStroke size="large" />}
                        disabled
                        noBorder
                    />
                    {renderDestinationsContainer()}
                </div>
            )}

            <Animate
                isOpen={searching}
                onOpen={onOpenAnimate}
                onClose={onCloseAnimate}
            >
                {handleClose && <Overlay onClick={handleClose} />}

                <Box
                    className={`${classes.root} ${!isOpenAnimation && classes.closed}`}
                >
                    <div className={classes.internalInput}>
                        {isMobile ? (
                            <a onClick={handleClose}>
                                <ArrowLeft />
                            </a>
                        ) : <MultipleDestinations noStroke size="medium" />}
                        <InputBase
                            placeholder="Para onde vai?"
                            onFocus={handleFocus}
                            onChange={handleChange}
                            onKeyUp={e => handleKeyUp(e, value)}
                            className={classes.flexGrow}
                            id="pesqinc-input"
                            autoComplete="off"
                        />
                    </div>
                    <div className={classes.boxResult}>
                        {!history && (!search || search.length === 0) && !mostSearched && (
                            <Typography
                                type="captionsemibold"
                                className={classes.listItemTitle}
                            >
                                Continue digitando…
                            </Typography>
                        )}
                        <List component="nav" className={classes.list}>
                            {search && search.length > 0 && (
                                <li className={classes.listItemTitle}>
                                    <Typography type="captionsemibold">
                                        Você está buscando por…
                                    </Typography>
                                </li>
                            )}
                            <div
                                className={`${!isMobile && classes.heightLimit} ${classes.scrolling}`}
                            >
                                {search &&
                                    search.map((item, i) => (
                                        <ListItem
                                            key={i}
                                            className={classes.listItem}
                                            button
                                            selected={i === index}
                                            onClick={() => handleSelect(item, i)}
                                        >
                                            {item.type === "location" && <MultipleDestinations size="small" noStroke />}
                                            <span className={classes.content}>
                                                <Typography
                                                    className={classes.name}
                                                    type="body2semibold"
                                                >
                                                    {item.name}
                                                </Typography>
                                                <Typography
                                                    className={classes.adressHotel}
                                                    type="body2regular"
                                                >
                                                    {" - "}
                                                    {item.address}
                                                </Typography>
                                            </span>
                                        </ListItem>
                                    ))}
                            </div>
                            {history && !(search && search.length > 0) && (
                                <li className={classes.listItemTitle}>
                                    <Typography type="captionsemibold">Últimas buscas</Typography>
                                </li>
                            )}
                            {history &&
                                !(search && search.length > 0) &&
                                history.map((item, i) => (
                                    <ListItem
                                        key={i}
                                        className={classes.listItem}
                                        button
                                        onClick={() => handleSelect(item, i)}
                                    >
                                        <Clock size="small" />
                                        <span className={classes.content}>
                                            <Typography className={classes.name} type="body2semibold">
                                                {item.name}
                                            </Typography>
                                        </span>
                                    </ListItem>
                                ))}
                            {mostSearched && !(search && search.length > 0) && (
                                <li className={classes.listItemTitle}>
                                    <Typography type="captionsemibold">
                                        Destinos mais buscados
                                    </Typography>
                                </li>
                            )}
                            {mostSearched &&
                                !(search && search.length > 0) &&
                                mostSearched.map((item, i) => (
                                    <ListItem
                                        key={i}
                                        className={classes.listItem}
                                        button
                                        onClick={() => handleSelect(item, i)}
                                    >
                                        {(item.type === "location" || !item.type) && (
                                            <MultipleDestinations size="small" noStroke />
                                        )}
                                        <span className={classes.content}>
                                            <Typography className={classes.name} type="body2semibold">
                                                {item.name}
                                            </Typography>
                                        </span>
                                    </ListItem>
                                ))}
                        </List>
                    </div>
                </Box>
            </Animate>
        </div>
    );
};

PesqincTour.propTypes = {
    classes: PropTypes.object.isRequired,
    search: PropTypes.array,
    history: PropTypes.array,
    mostSearched: PropTypes.array,
    onChange: PropTypes.func,
    onFocus: PropTypes.func,
    onClose: PropTypes.func,
    onSelect: PropTypes.func,
    value: PropTypes.array,
    selectedIndex: PropTypes.number,
    transparent: PropTypes.bool,
    isMobile: PropTypes.bool,
    error: PropTypes.bool,
    errorMsg: PropTypes.string,
    destinations: PropTypes.array,
};

const mergeStyles = theme => {
    return {
        ...styles(theme),
        ...tourClasses(theme),
    };
};
export default withStyles(mergeStyles, { withTheme: true })(PesqincTour);
